"use client";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

interface Article {
  title: string;
  description?: string;
  url?: string;
  source?: string;
  urlToImage?: string;
  publishedAt?: string;
}

export default function NewsCarousel({ news }: { news: Article[] }) {
  const settings = {
    dots: true,
    infinite: true,
    speed: 600,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 5000
  };

  return (
    <Slider {...settings}>
      {news.map((n, i) => (
        <div key={i} className="px-4">
          <div
            className="rounded-xl overflow-hidden shadow-lg cursor-pointer bg-white"
            onClick={() => window.open(n.url, "_blank")}
          >
            {n.urlToImage && (
              <img
                src={n.urlToImage}
                alt={n.title}
                className="w-full h-64 object-cover"
              />
            )}
            <div className="p-4">
              <h2 className="text-lg font-bold mb-2">{n.title}</h2>
              {n.publishedAt && (
                <p className="text-xs text-gray-500">
                  {new Date(n.publishedAt).toLocaleDateString("es-MX")}
                </p>
              )}
              {n.description && (
                <p className="text-gray-600 text-sm">{n.description}</p>
              )}
              {n.source && (
                <small className="text-gray-500"> â€” {n.source}</small>
              )}
            </div>
          </div>
        </div>
      ))}
    </Slider>
  );
}
